






































































































































import Logo from "../misc/Logo.vue";
import ProgressCard from "./ProgressCard.vue";
import NumberCard from "./NumberCard.vue";
import ChartCard from "./ChartCard.vue";
import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import {
  settingsModule,
  jobsModule,
  repositoriesModule,
  authModule
} from "@/store/modules";
import { format } from "date-fns";
import JobExecutionCalendar from "@/components/misc/JobExecutionCalendar.vue";
import RepositoriesList from "@/components/repositories/RepositoriesList.vue";
import JobsList from "@/components/jobs/JobsList.vue";
import JobWizard from "@/components/home/JobWizard.vue";
import RestoreWizard from "@/components/home/RestoreWizard.vue";
import { getOverviewStats } from "@/store/api";
import { IGetOverviewStatsResponse, Months } from "../../shared";

@Component({
  components: {
    ChartCard,
    ProgressCard,
    Logo,
    NumberCard,
    JobExecutionCalendar,
    RepositoriesList,
    JobsList,
    JobWizard,
    RestoreWizard
  }
})
export default class HomeOverviewClass extends Mixins() {
  jobWizardOpen = false;
  restoreWizardOpen = false;

  get allRepos() {
    return repositoriesModule.allRepositories;
  }

  get allJobs() {
    return jobsModule.allJobs;
  }

  get numberOfBackupJobs() {
    return jobsModule.allJobs.length;
  }
  get numberOfRepositories() {
    return repositoriesModule.allRepositories.length;
  }

  get orderedJobsWithNextExecutions() {
    return jobsModule.orderedJobsWithNextExecutions;
  }

  get nextJobExecutionDate() {
    return this.orderedJobsWithNextExecutions.length
      ? this.$d(this.orderedJobsWithNextExecutions[0].executionDates[0], "long")
      : "";
  }

  interval = 0;
  value = 0;

  stats: IGetOverviewStatsResponse | null = null;

  // beforeDestroy() {
  //   clearTimeout(this.interval);
  // }

  mounted() {
    this.getStats();
  }

  async getStats() {
    const response = await getOverviewStats(authModule.userId);
    if (response.payload) {
      this.stats = response.payload;
    }
  }

  // nextTimeout() {
  //   if (this.value >= 100) {
  //     this.value = 0;
  //   }
  //   this.value += Math.floor(Math.random() * 10);
  //   this.value = Math.min(100, this.value);
  //   this.interval = setTimeout(this.nextTimeout, Math.random() * 1000 + 500);
  // }

  get greeting() {
    return this.$t("home.greeting", {
      name: settingsModule.fullUserName
    });
  }

  get chartData() {
    const labels = [
      this.$t("month.jan"),
      this.$t("month.feb"),
      this.$t("month.mar"),
      this.$t("month.apr"),
      this.$t("month.may"),
      this.$t("month.jun"),
      this.$t("month.jul"),
      this.$t("month.aug"),
      this.$t("month.sep"),
      this.$t("month.oct"),
      this.$t("month.nov"),
      this.$t("month.dec")
    ];
    if (!this.stats) {
      const emptySeries = new Array(12).fill(0);
      return { labels, series: new Array(2).fill(emptySeries) };
    }
    const executions = this.stats.jobExecutionCount;
    const months: Months[] = [
      "january",
      "february",
      "march",
      "april",
      "may",
      "june",
      "july",
      "august",
      "september",
      "october",
      "november",
      "december"
    ];
    const series: number[][] = [[], []];
    for (const month of months) {
      const execution = executions[month];
      if (execution) {
        series[0].push(execution.failedExecutions);
        series[1].push(execution.successfulExecutions);
      } else {
        series[0].push(0);
        series[1].push(0);
      }
    }
    return {
      labels,
      series
    };
  }
}
